.project_container{
  height: 400px;
  width: 100%;
  border: solid 1px transparent;
  border-radius: 15px;
  background-color: white;
  box-shadow: white 0px 2px 8px 0px;
  overflow-y: scroll;
     .close_icon{
      //border: solid 1px black;
      float: right;
      font-size: 20px;
      margin: 1% 3% 0 0;
      color: #ff0000;
  }
  .close_icon:hover{
      background-color: red;
      color: white;
  }
  .project_profile_table_container{
      width: 98%;
      border-collapse: collapse;
      background-color: white;
      margin: 5% auto;
      

      .project_profile_row{
          border: solid 1px  rgb(7, 78, 170);
          background-color:  rgb(7, 78, 170);
          .project_row_data{
              padding: 5px 0 5px 30px;
              font-size: 16px;
              color: white;
          }
      }
      .project_profile_body_row{
          border-bottom: solid 1px #2424;
          .project_profile_body_row_data{
              text-align: left;
              padding: 5px 0 3px 30px;
          }
          
      }
  }
  .project_profile_body_row:hover{
    background-color: rgba(172, 172, 172, 0.178);
    border-left: 2px solid rgb(7, 78, 170);
    border-right: 2px solid rgb(7, 78, 170);            
  }
 
}
::-webkit-scrollbar{
    width: 3px;
}
::-webkit-scrollbar-thumb{
    border-radius: 10px;
    background-color:rgb(7, 78, 170);
}
::-webkit-scrollbar-button {
    height: 30px;
  }

